:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  --max-width: 1170px;
  --fixed-width: 450px;
  --clr-orange-1: hsl(12, 83%, 98%);
  --clr-orange-2: hsl(14, 91%, 95%);
  --clr-orange-3: hsl(12, 89%, 89%);
  --clr-orange-4: hsl(13, 87%, 82%);
  --clr-orange-5: hsl(13, 88%, 68%);
  --clr-orange-6: hsl(13, 88%, 55%);
  --clr-orange-7: hsl(13, 74%, 49%);
  --clr-orange-8: hsl(13, 74%, 33%);
  --clr-orange-9: hsl(13, 73%, 25%);
  --clr-orange-10: hsl(13, 73%, 16%);
}

.all {
    min-width: 82.5em;
    width: 84em;
    min-height: 41.25em; 
    background-color: green;
    display: grid;
    grid-template-rows: 6.38em 35em;
    grid-template-areas: "top" "bottom";
}

.topNavBar {
    grid-area: top;
    display: grid;
    grid-template-columns: 28.33em 56.66em;
    grid-template-areas: "left right";
    background-color: #0a4385;
    align-items: center;
    justify-items: center;
}

.startLogo {
    grid-area: left;
    padding: 1.25em 0.625em 1.25em 0.625em;
}

.lmSystem {
    grid-area: right;
    color: yellow;
    font-size: 2.63em;
}

.bottompart {
    grid-area: bottom;
    background-color: white;
    display: grid;
    grid-template-columns: 15.63em 68.88em;
    grid-template-areas: "bottomleft bottomright";
}

.sidenav {
    grid-area: bottomleft;
    height: 100%;
    background-color: red;
}

.bottomrightpart {
    grid-area: bottomright;
    height: 100%;
    color: blue;
    background-color: yellow;
    display: grid;
    grid-template-rows: 4.5em 31.56em;
    grid-template-areas: 
    "leadsCenteredPart"
    "leadsViewPort";
}

/* The navigation menu links */
.sidenav a {
    padding: 0.375em 0.5em 0.375em 1em;
    text-decoration: none;
    font-size: 1.5625em;
    display: block;
}

.btn {
  display: inline-block;
  width: 100%;
  margin-left: -0.25em;
  background: var(--clr-primary-5);
  color: var(--clr-white);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius);
  border-color: transparent;
  text-transform: capitalize;
  font-size: 1.19em;
  letter-spacing: var(--spacing);
  cursor: pointer;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  transition: var(--transition);
}

.btn:hover {
  background: var(--clr-primary-1);
  color: var(--clr-primary-5);
}

.submissionForm {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 3.125em 21.75em 3.125em;
    grid-template-areas: 
    "formNameDescription"
    "inputHere"
    "submitButton";
    font-size: 1.25em;
}

.submissionInputZone {
    height: auto;
    width: 100%;
    grid-area: inputHere;
    display: grid;
    grid-template-rows: 2.7em 2.7em 9em 2.7em 2.7em 2.7em;
    grid-template-columns: 1fr 4fr;
    grid-template-areas: 
    "customerNameDescription customerNameform"
    "segmentDetailsDescription segmentDetailsform"
    "detailsDescription detailsform"
    "dollarvalueDescription dollarvalueform"
    "submittedToDescription submittedToform"
    "submittedByDescription submittedByform"
}

.formNameZone {
    grid-area: formNameDescription;
    text-align: center;
    padding-top: 0.625em;
}

.customerNameDescriptionClass {
    grid-area: customerNameDescription;
    padding-top: 0.625em;
    padding-left: 3.75em;
}

.detailsDescriptionClass {
    grid-area: detailsDescription;
    padding-top: 0.625em;
    padding-left: 3.75em;
}

.segmentDetailsDescriptionClass {
    padding-top: 0.625em;
    padding-left: 3.75em;
    grid-area: segmentDetailsDescription;
}

.submittedToDescriptionClass {
    grid-area: submittedToDescription;
    padding-top: 0.625em;
    padding-left: 3.75em;
}

.submittedByDescriptionClass {
    grid-area: submittedByDescription;
    padding-top: 0.625em;
    padding-left: 3.75em;
}

.submitButtonZone {
    grid-area: submitButton;
    display: flex;
    justify-content: center;
}

.customerNameInputClass {
    grid-area: customerNameform;
}

.detailsInputClass {
    grid-area: detailsform;
}

.segmentDetailsInputClass {
    grid-area: segmentDetailsform;
}

.submittedToInputClass {
    grid-area: submittedToform;
}

.submittedByInputClass {
    grid-area: submittedByform;
}

.dollarvalueInputClass {
  grid-area: dollarvalueform;
}

.dollarvalueDescriptionClass {
  grid-area: dollarvalueDescription;
  padding-top: 0.625em;
    padding-left: 3.75em;
}

input[type=text], select, textarea{
  width: 90%;
  padding: 0.75em;
  border: 1px solid #ccc;
  border-radius: 0.25em;
  box-sizing: border-box;
  resize: vertical;
}

.btn2 {
  display: inline-block;
  width: 20%;
  height: auto;
  background: var(--clr-primary-5);
  color: var(--clr-white);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius);
  border-color: transparent;
  text-transform: capitalize;
  font-size: 1.375em;
  letter-spacing: var(--spacing);
  cursor: pointer;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  transition: var(--transition);
}

.btn2 :hover {
  background: var(--clr-primary-1);
  color: var(--clr-primary-5);
}

.item {
  background: var(--clr-white);
  display: flex;
  justify-content: space-between;
  max-width: var(--fixed-width);
  margin: 2rem auto;
  align-items: center;
  border-radius: var(--radius);
}
.item button,
.item a {
  background: transparent;
  border-color: transparent;
  color: var(--clr-primary-5);
  letter-spacing: var(--spacing);
  cursor: pointer;
}
.item {
  padding: 1rem 2rem;
}
.item h4 {
  margin-bottom: 0;
}
.item p {
  margin-bottom: 0;
}

.viewLeads {
  background-color: cyan;
  grid-area: leadsViewPort;
  overflow-y: scroll;
}

.users {
  display: grid;
  grid-template-columns: 1fr;
  margin: 1rem 0.6vw;
}
.users li {
  width: 67.125em;
  height: 8.625em;
  margin-left: -2.625em;
  margin-top: 0.4125em;
  display: grid;
  grid-template-columns: 0.875em 43.75em 21.625em 0.875em;
  grid-template-areas:
  "leftmargin detailspart otherdetailspart rightmargin";
  align-items: center;
  background: var(--clr-white);
  padding-top: 0vh;
  padding-bottom: 0vh;
  border-radius: var(--radius);
  text-align: left;
}

.extraDetails {
  width: 67.125em;
  height: 8.75em;
  background-color: yellow;
}

.black-background {
  height: 7.5em;
  grid-area: detailspart;
  display: grid;
  grid-template-rows: 5.625em 1.875em;
  grid-template-areas: "customernamezone" "submittedtobyzone";
  align-items: center;
  justify-items: center;
  background: black;
}
.red-background {
  grid-area: otherdetailspart;
  height: 7.5em;
  color: yellow;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-areas: 
  "segment"
  "buttonforstatus";
  align-items: center;
  justify-items: center;
  background: red;
}
.leadsCentered {
  grid-area: leadsCenteredPart;
  text-align: center;
}
.segmentDet {
  grid-area: segment;
}

.buttonandstatus {
  width: 90%;
  grid-area: buttonforstatus;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "statusdisplay clicktochange";
}

.statusbtn {
  grid-area: statusdisplay;
  display: inline-block;
  width: auto;
  margin-left: 0.9375em;
  text-align: center;
  background: var(--clr-primary-5);
  color: var(--clr-white);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius);
  border-color: transparent;
  text-transform: capitalize;
  font-size: 1.375em;
  letter-spacing: var(--spacing);
  cursor: pointer;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  transition: var(--transition);
}

.viewDetails {
  grid-area: clicktochange;
  text-align: center;
}
.submitToByArea {
  grid-area: submittedtobyzone;
  margin-top: -1.5625em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "ToArea ByArea";
}
.submitToArea {
  margin-right: 3.75em;
  grid-area: ToArea;
}
.submitByArea {
  margin-left: 3.75em;
  grid-area: ByArea;
}
.custnameArea {
  grid-area: customernamezone;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "nameZone dollarValueZone";
}
.nameonlyArea {
  padding-left: 3.125em;
  font-size: 2.1875em;
  grid-area: nameZone;
}
.dollarValueArea {
  text-align: right;
  padding-right: 3.125em;
  font-size: 2.1875em;
  grid-area: dollarValueZone;
}

.btnview {
  display: inline-block;
  margin-left: 2.8125em;
  width: 70%;
  background: lime;
  color: blue;
  z-index: 200;
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius);
  border-color: transparent;
  text-transform: capitalize;
  font-size: 0.75em;
  letter-spacing: var(--spacing);
  cursor: pointer;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  transition: var(--transition);
}

.btnview:hover {
  background: var(--clr-primary-1);
  color: var(--clr-primary-5);
}

.defaultInput {
  height: 70%;
}

textarea {
  font-size: 1.25rem;
}

.projectDetailZone {
  margin-left: 0.625em;
  margin-right: 0.625em;
  padding-right: 0.625em;
  padding-left: 0.625em;
  margin-top: 0.625em;
  padding-top: 0.25em;
  padding-bottom: 0.25em;
  background-color: lime;
}

.firstButton {
  width: 20%;
  margin-left: 1.25em;
  margin-top: 0.9375em;
}